<script>
import * as L from 'leaflet'

export default {
  name: 'Popup',
  props: {
    items: Array,
    geoJson: Object,
    center: Array

  },
  data() {
    return {
      map: undefined,
      visible: true,
      counter: 0,
      marker: undefined

    }
  },
  mounted() {
    this.map = L.map('popup-map', {
    continuousWorld: true,
    zoomControl: false,
     dragging: false,
    scrollWheelZoom: false,
    static: true
    }).setView(this.center ? this.center : [54.904571379248125, -3.436872187499991], 7.5);


    L.geoJson(this.geoJson, {
      clickable: true,
    }).addTo(this.map);

    this.updateMarker();

  },
  watch: {
    center: function() {
      this.updateMarker();
    },
    items: function() {
      this.updateMarker();
    }
  },
  methods: {
    updateMarker() {
      if(this.map) {
        if(this.marker) {
          this.map.removeLayer(this.marker);
        }

        this.marker = L.marker(this.center, {
          icon: L.divIcon({
            html: '<svg width="32px" fill="#000000" version="1.1" x="0px" y="0px" viewBox="0 0 32 50.036" enable-background="new 0 0 100 100" xmlns="http://www.w3.org/2000/svg" xmlns:bx="https://boxy-svg.com"><circle style="fill: rgb(255, 255, 255);" cx="16" cy="21" r="9.625"/><g transform="matrix(0.440792, 0, 0, 0.440792, -6.039605, 4.79604)"><path d="M50,5C32.459,5,18.239,19.22,18.239,36.761C18.239,54.302,50,95,50,95s31.761-40.698,31.761-58.239   C81.761,19.22,67.541,5,50,5z M50.183,56.702c-11.206,0-20.29-9.084-20.29-20.29c0-11.205,9.084-20.289,20.29-20.289   c11.206,0,20.289,9.084,20.289,20.289C70.473,47.618,61.389,56.702,50.183,56.702z" style="fill: rgb(245, 219, 55); fill-opacity: 1;"/></g><text x="15.993" y="26.593" style="white-space: pre; fill: rgb(51, 51, 51); font-size: 14.3px; text-anchor: middle;">' + this.items.length + '</text></svg>',
            iconSize: new L.Point(32, 50)
          })
        }).addTo(this.map);

        this.map.panTo(new L.LatLng(this.center[0], this.center[1]));
      }
    },
    getId(url) {
      return this.$youtube.getIdFromUrl(url)
    },
    playVideo() {
      this.player.playVideo()
    },
    stopVideo() {
      this.player.stopVideo()
    },

    close() {
      this.$emit("close");
    }
  },
  computed: {
    isMobile() {
      return window.innerWidth <= 768;
    },
    player() {
      return this.$refs.youtube.player
    },
    item() {
      if (this.items[this.counter]) {
        return this.items[this.counter];
      }
      return null;
    },
    themeTags() {
      if (this.item) {
        return this.item.themeTags.split(',');
      }
      return [];
    },
  }
}
</script>

